/* GENERAL */
body {
  margin: 0;
}

* {
  font-family: 'Bebas Neue', cursive;
  letter-spacing: 1px;
}

/* START NAV  */

.navbar {
  position: relative;
  display: flex;
  top: 0px;
  flex-direction: row;
  width: 100%;
  background-color: #40649e98;
  height: 100px;
  z-index: 999;
}

ul {
  list-style-type: none;
}

.image-logo {
  height: 120px;
  top: 70px;
}

.logo {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0px;
  display: inline-block;
  transform: translatex(-80px);
}

.navbar-nav {
  display: flex;
  position: absolute;
  right: 0;
  top: 15%;
  margin: 10px;
  padding: 10px;
  bottom: 0;
  width: 120px;
  justify-content: space-between;
}

/* END NAV */

/* START HEADER */
.main__wrapper {
  margin-right: auto;
  margin-left: auto;
  max-width: 760px;
  padding-right: 10px;
  padding-left: 10px;
}
.hero-image {
  width: 100%;
  max-width: none;
  height: auto;
}

/* END HEADER */

/* START ARTICLE */
.main__item-title {
  border-radius: 5px;
  background-color: #fff04e;
  padding: 0.5rem 1rem;
}
p {
  font-family: 'Roboto', sans-serif;
}

/* END ARTICLE */
/* START NEWSLETTER */
form {
  border: 3px solid #f1f1f1;
  border-radius: 5px;
  margin-bottom: 5rem;
}
.h2__color {
  color: #fff04e;
}
.form__container {
  padding: 20px;
  background-color: #023255;
  border-radius: 5px;
}

input[type='text'],
input[type='submit'] {
  width: 100%;
  padding: 12px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
  border-radius: 5px;
}

input[type='checkbox'] {
}

input[type='submit'] {
  background-color: #fff04e;
  color: #023255;
  border: none;
  border-radius: 5px;
}

input[type='submit']:hover {
  opacity: 0.8;
}

/* END NEWSLETTER */
